.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #FFFFFF;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Navigation bar styles */
.Navbar {
  background-color: #20232a;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.Navbar-list {
  list-style-type: none;
  display: flex;
  gap: 1.5rem;
  margin: 0;
  padding: 0;
}

.Navbar-item a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  transition: all 0.3s ease-in-out;
  border-radius: 5px;
}

.Navbar-item a:hover {
  background-color: #61dafb;
  color: black;
  transform: scale(1.1);
  cursor: pointer;
}

/* Active link styles */
.Navbar-item.active a {
  background-color: #61dafb;
  color: black;
  font-weight: bold;
  transform: scale(1.1);
}

/* Content styles */
.Content {
  padding: 2rem;
  text-align: left;
  max-width: 800px;
  margin: 0 auto;
}

.Content h2 {
  color: #61dafb;
  font-size: 2rem;
  margin-bottom: 1rem;
}

/* Logout button styles */
.btn-logout {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  margin-left: 1rem;
  transition: background-color 0.3s ease;
}

.btn-logout:hover {
  background-color: #c0392b;
}